import axios from 'axios'


const postPhoto = async (formData) => {
    const config = {
        headers: { 
            'Content-Type': 'multipart/form-data'
            }
        }
    const response = await axios.post('/photos', formData, config)
    return response
}

const getPhotos = () => {
    const request = axios.get('/photos')
    return request.then(response => response.data)
}

const login = async (credentials) =>{
    const response = await axios.post('http://localhost:5000/users/login', credentials)
    return response.data
}

export default {postPhoto, getPhotos, login}